.Prensa {
  background-image: url("../../assets/sucursales-bg-2.jpg");
  background-size: 100%;
  background-color: $color-secondary;
}

.prensa-container {
  width: 100%;
  padding: 2vw 0;
  overflow: hidden;
  position: relative;
  border-radius: 1vw;
  background-color: rgba(0, 0, 0, 0.3);
}

.arrow-icon-container {
  width: 4vw;
  height: 4vw;
  position: absolute;
  top: 27vw;
  transition: 0.3s;
  will-change: opacity;
  &__left {
    left: 8vw;
  }
  &__right {
    right: 8vw;
    transform: rotateZ(180deg);
  }
  &__active {
    opacity: 0.8;
    cursor: pointer;
    &:hover {
      opacity: 1;
    }
  }
  &__inactive {
    opacity: 0.3;
    pointer-events: none;
  }
  & img {
    width: 100%;
  }
}

.prensa-box-wrapper {
  width: 200%;
  display: flex;
  transition: 1s;
  will-change: margin;
  &__page1 {
    margin-left: 0;
  }

  &__page2 {
    margin-left: -100%;
  }
}

.prensa-box-container {
  width: 33.33%;
  padding: 1vw 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.prensa-box {
  width: 80%;
  padding-bottom: 2vw;
  border-radius: 6px;
  cursor: pointer;
  overflow: hidden;
  background-color: $color-white2;
  &:hover .prensa-box-image-container img {
    transform: scale(1.02);
  }
  &:hover .prensa-box-title {
    text-decoration: underline;
  }
}

.prensa-box-image-container {
  width: 100%;
  height: 14vw;
  overflow: hidden;
  margin-bottom: 2vw;
  & img {
    width: 100%;
    transition: 0.3s;
    will-change: transform;
  }
}

.prensa-box-title {
  width: 85%;
  margin: 0 auto;
  transition: 0.3s;
  will-change: color;
}
