.Navbar {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  z-index: 10;
  top: 0;
  width: 100%;
  padding-top: 4vw;
  background: rgb(0, 0, 0);
}

.navbar-menu-icon {
  width: 8vw;
  position: absolute;
  left: 10%;
  top: 7vw;
  color: $color-white;
}

.navbar-logo-container {
  width: 40vw;
  & img {
    width: 100%;
  }
}

.navbar-menu-container {
  display: none;
}

.navbar-media-container {
  display: none;
}

@media screen and (min-width: 1099px) {
  .Navbar {
    justify-content: flex-start;
    padding-top: 0;
  }

  .navbar-menu-icon {
    display: none;
  }

  .navbar-logo-container {
    width: 8vw;
    cursor: pointer;
    margin-left: 1vw;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .navbar-menu-container {
    display: flex;

    width: 45%;
    margin: 0 0 0 3.3vw;
  }
  .navbar-menu-list {
    list-style: none;
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  .navbar-menu-links {
    font-size: 0.9vw;
    transition: 0.3s;
    will-change: color;
    cursor: pointer;

    &:hover {
      color: $color-variation;
    }
    &__inactive {
      color: $color-white;
    }
    &__active {
      color: $color-variation;
    }
  }

  .navbar-media-container {
    display: flex;
    justify-content: space-between;
    position: absolute;
    right: 2.5vw;
    width: 8.5vw;
  }

  .navbar-media-links {
    width: 2.5vw;
    height: 2.5vw;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 1;
    will-change: opacity background-color;
    border-radius: 100vw;
    transition: 0.3s;
    background-color: $color-primary;
    &:hover {
      background-color: $color-terciary;
      opacity: 1;
    }
  }

  .media-image {
    filter: brightness(200%) grayscale(100%);
    transition: filter 0.3s ease;
    &:hover {
      filter: none;
    }
  }

  .media-ig {
    width: 60%;
  }
  .media-in {
    width: 45%;
  }
  .media-fb {
    width: 50%;
  }
}
