.Modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 11;
  top: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
}

.modal-container {
  width: 50%;
  height: 80vh;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: auto;
  border-radius: 6px;
  background-color: $color-white;
}

.close-icon {
  width: 1.5vw;
  height: 1.5vw;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 1.5vw;
  right: 1.5vw;
  opacity: 0.6;
  will-change: opacity;
  transition: 0.3s;
  cursor: pointer;
  & img {
    width: 100%;
  }
  &:hover {
    opacity: 1;
  }
}

.modal-title {
  display: flex;
  align-items: center;
  font-size: 1.4vw;
  margin-bottom: 1.5vw;
  & img {
    width: 2vw;
  }
}

.modal-container iframe {
  box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.1);
  border-radius: 6px;
}
