.Academia {
  background-color: $color-secondary;
}

.academia-box-container {
  margin-bottom: 3vw;
}

.academia-box {
  margin: 0 auto 2vw;
  width: 80%;
}

.logo-container {
  width: 90%;
  border-radius: 100vw;
  padding-right: 5vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.6);
  & img {
    width: 15vw;
  }
}

.cmc-text {
  width: 75%;
}

.mariano-box-container {
  width: 70%;
  margin-top: 3vw;
  padding: 3vw;
  &:hover .mariano-image {
    transform: scale(1.05);
  }
}

.academia-subcontainer {
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.academia-subcontainer figure {
  width: 23vw;
  overflow: hidden;
}

.mariano-image {
  width: 100%;
  will-change: transform;
  transition: 0.3s;
}

.mariano-box {
  width: 40%;
  text-align: left;
  font-size: 1vw;
}
