@font-face {
  font-family: "Merriweather";
  src: local("Merriweather"),
    url("../../fonts/Merriweather/Merriweather-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: "Poppins";
  src: local("Poppins"),
    url("../../fonts/Poppins/Poppins-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: "Poppins";
  src: local("Poppins"),
    url("../../fonts/Poppins/Poppins-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

* {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  box-sizing: border-box;
}

h1,
h3,
h4,
h5,
h6 {
  font-family: "Merriweather", sans-serif;
}

h1 {
  width: 100%;
  font-size: 4vw;
  text-transform: uppercase;
  color: $color-variation;
}

h2 {
  font-size: 2vw;
  line-height: 120%;
  color: $color-white;
}

h3 {
  width: 100%;
  text-align: center;
  font-size: 2vw;
  font-weight: 500;
  text-transform: uppercase;
  line-height: 120%;
  margin: 0 0 5vw;
  color: $color-variation;
}

.Section {
  position: relative;
  z-index: 1;
  background-position: center;
  background-repeat: no-repeat;
}

.Overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  z-index: 2;
  &__black {
    background-color: rgba(0, 0, 0, 0.7);
  }
  &__white {
    background-color: rgba(255, 255, 255, 0.75);
  }
}

.BlackOverlay {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.8);
}

.Box {
}

.BoxTitle {
  font-family: "Merriweather", sans-serif;

  text-align: center;
  margin: 0 0 1vw;
  text-transform: uppercase;
  color: $color-variation;
}

@media screen and (min-width: 1099px) {
  h1 {
    font-size: 5vw;
    margin: 0 0 0.5vw;
    padding: 0;
    line-height: 110%;
  }
  h2 {
    font-size: 1.5vw;
    line-height: 120%;
    margin: 0 0 2vw;
  }

  h3 {
    font-size: 4vw;
    font-weight: 400;
    line-height: 110%;
    margin: 0;
  }

  button {
    font-size: 1vw;
  }

  .Subtitle {
    text-align: center;
    margin: 1vw 0 0;
    font-size: 1.2vw;
    &__white {
      color: $color-white;
    }
    &__black {
      color: $color-secondary;
    }
    &__variation {
      color: $color-variation;
    }
  }

  .Text {
    font-size: 1vw;
    text-align: center;
    &__white {
      color: $color-white;
    }
    &__black {
      color: $color-primary;
    }
  }

  .SectionContainer {
    margin-top: 3vw;
  }

  .BoxContainer {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }

  .Box {
    padding: 2vw;
    border-radius: 8px;
    border: 2px solid $color-terciary;
    background-color: rgba(0, 0, 0, 0.3);
    transition: 0.5s;
    will-change: border;
    &__border {
      &:hover {
        border: 2px solid $color-variation-dark;
      }
    }
  }

  .BoxTitle {
    font-size: 1.5vw;
    margin: 0 0 1vw;
  }

  .BoxText {
    font-size: 0.9vw;
    text-align: center;
    color: $color-white;
  }
}
