.LayoutContainer {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 7vw 0 7vw;
  position: relative;
  z-index: 3;
}

.LayoutContainer--wrapper {
  width: 75%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
