.Servicios {
  background-image: url(../../assets/services-bg6.jpg);
  background-size: 110%;
  background-color: $color-white;
}

.services-container {
  display: flex;
  justify-content: space-evenly;
}

.services-subcontainer {
  width: 50%;
  display: flex;
  flex-direction: column;
}

.services-description {
  width: 100%;
  margin: 0 0 1vw;
  font-size: 1.05vw;
  color: $color-secondary;
}

.services-image-container {
  width: 28.4vw;
  height: 38.2vw;
  margin-left: 6vw;
  overflow: hidden;

  position: relative;
}

.services-image {
  width: 28.4vw;
  height: 38.2vw;
  background-size: 102%;
  background-position: center;
  background-repeat: no-repeat;
  animation-duration: 15s;
  animation-iteration-count: infinite;
  position: absolute;
  will-change: margin;
  &__1 {
    background-image: (url("../../assets/services3.JPG"));
    animation-name: imageAnimation1;
  }
  &__2 {
    background-image: (url("../../assets/services1.jpg"));
    animation-name: imageAnimation2;
  }
  &__3 {
    background-image: (url("../../assets/services5.JPG"));
    animation-name: imageAnimation3;
  }
}

.services-list {
  list-style: none;
  padding: 2vw 3vw;
  background-color: $color-primary;
  background-color: rgba(0, 0, 0, 0.85);
}

.services-item {
  margin: 1.4vw 0;
  font-size: 1.2vw;
  display: flex;
  align-items: center;
  cursor: default;
  color: $color-white;
  & img {
    width: 2vw;
    margin-right: 1vw;
  }
  &:hover {
    color: $color-variation;
  }
}

@keyframes imageAnimation1 {
  0% {
    margin-top: 0;
    z-index: 2;
  }

  29% {
    margin-top: 0;
    z-index: 1;
  }

  41% {
    margin-top: 0;
    z-index: 1;
  }
  43% {
    margin-top: -39vw;
    z-index: 1;
  }

  93% {
    margin-top: -39vw;
    z-index: 2;
  }

  100% {
    margin-top: 0;
    z-index: 2;
  }
}

@keyframes imageAnimation2 {
  0% {
    margin-top: 39vw;
    z-index: 1;
  }

  30% {
    margin-top: 39vw;
    z-index: 1;
  }

  37% {
    margin-top: 0;
    z-index: 1;
  }

  70% {
    margin-top: 0;
    z-index: 1;
  }

  73% {
    margin-top: 39vw;
    z-index: 1;
  }

  100% {
    margin-top: 39vw;
    z-index: 1;
  }
}

@keyframes imageAnimation3 {
  0% {
    margin-left: 0;
    z-index: 1;
  }

  1% {
    margin-left: 0;
    z-index: 1;
  }

  3% {
    margin-left: 105%;
    z-index: 1;
  }

  32% {
    margin-left: 105%;
    z-index: 1;
  }
  33% {
    margin-left: 105%;
    z-index: 1;
  }

  60% {
    margin-left: 105%;
    z-index: 1;
  }

  67% {
    margin-left: 0;
    z-index: 1;
  }

  100% {
    margin-left: 0;
    z-index: 1;
  }
}
