.Hero {
  height: 100vh;
  background-size: 120%;
  background-position: right top;
}

.hero-container {
  width: 85%;
  margin: 10vw auto 0;
}

.hero-container h2 {
  width: 70%;
  font-weight: 400;
}

@media screen and (min-width: 1099px) {
  .Hero {
    background: rgb(0, 0, 0, 0);
    background-size: 120%;
    background-position: -120% top;
  }

  .hero-container {
    width: 100%;
    margin: 0 auto;
  }

  .hero-container h2 {
    width: 30%;
    margin: 0.5vw 0 1.5vw;
  }

  .hero-container img {
    width: 20vw;
  }
}
