.Sucursales {
  background-color: white;
  background-image: url("../../assets/beneficios-bg4.jpeg");

  background-size: 100%;
  position: relative;
}

.sucursales-container {
  width: 80%;
  padding: 3vw 0;
  border-radius: 6px;
  border: 2px solid $color-terciary;
  background-color: rgba(17, 17, 17, 0.85);
}

.sucursales-subcontainer {
  margin: 0 0 0 12%;
  width: 80%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.sucursales-button-container {
  width: 50%;
  margin: 0.8vw 0;
  display: flex;
  align-items: center;
  will-change: color;
  & img {
    width: 1.5vw;
    margin-right: 0.5vw;
  }
}

.sucursales-button {
  font-size: 1.1vw;
  transition: 0.3s;
  cursor: pointer;
  color: $color-white;
  &:hover {
    color: $color-variation;
  }
}
