.Beneficios {
  background-image: url("../../assets/beneficios-bg4.jpeg");
  background-image: url("../../assets/sucursales-bg-3.jpg");

  background-image: url("../../assets/beneficios-bg.svg");
  background-image: url("../../assets/bg-benef1.jpg");
  background-size: 100%;
  background-position: center 60%;
  background-color: $color-secondary;
}

.brands-container {
  width: 100%;
  padding: 1vw 0;
  border-radius: 1vw;
  overflow: hidden;
  border: 2px solid $color-terciary;

  background-color: rgba(17, 17, 17, 0.9);
}

.brands-wrapper {
  width: 260%;
  margin-left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  animation-name: logosAnimation;
  animation-duration: 120s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-direction: normal;
  will-change: margin;
}

.brands-logo-container {
  width: 20%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.brands-logo-image {
  transition: 0.5s;
  will-change: transform;
  &__osde {
    width: 50%;
  }
  &__ase {
    width: 60%;
  }
  &__swiss {
    width: 80%;
  }
  &__medife {
    width: 60%;
  }
  &__sport {
    width: 50%;
  }
  &__shell {
    width: 50%;
  }
  &__nacion {
    width: 60%;
  }
  &__brubank {
    width: 60%;
  }
  &__siglo {
    width: 90%;
  }
  &__amigos {
    width: 60%;
  }
  &__usal {
    width: 60%;
  }
  &__paseo {
    width: 100%;
  }
  &__ankura {
    width: 60%;
  }
  &:hover {
    transform: scale(1.2);
  }
}

@keyframes logosAnimation {
  0% {
    margin-left: 0%;
  }
  100% {
    margin-left: -160%;
  }
}
